import { BoardManager } from "./BoardManager";
import { GameVars } from "../../GameVars";
import { ButtonEx } from "../../utils/ButtonEx";
import { AudioManager } from "../AudioManager";
import { GAMESNACKS } from "../../Gamesnacks";

export class GUI extends Phaser.GameObjects.Container {

    private menuBtn: ButtonEx;

    constructor(scene: Phaser.Scene) {

        super(scene);

        this.scaleY = GameVars.scaleY;

        this.menuBtn = new ButtonEx(this.scene, GameVars.desktop ? 55 : 70, GameVars.desktop ? 55 : 70, "texture_atlas_1", "btn_menu");
        this.menuBtn.onUp(this.onClickMenu, this);
        this.add(this.menuBtn);

        if (GameVars.onTutorial) {
            this.menuBtn.visible = false;
        }
    }

    public hideMenu(): void {
        this.menuBtn.visible = false;
    }

    public showMenu(): void {

        this.menuBtn.visible = true;
        this.menuBtn.alpha = 0;

        this.scene.tweens.add({
            targets: this.menuBtn,
            alpha: 1,
            ease: Phaser.Math.Easing.Cubic.Out,
            duration: 500
        });
    }

    private onClickMenu(): void {
        BoardManager.showMenu();
    }
}
